@import "../../assets/scss/Colors.module.scss";
@import "../../assets/scss/Fonts.module.scss";

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 10px 13px -7px $Shadow;
  position: relative;
  padding: 15px;
  background: linear-gradient(135deg, #ffeafe 0%, #ffd8fb 50%, #ec9fde 100%);
  transition: transform 0.3s ease;
  position: relative;
  min-width: 250px;
  min-height: 400px;

  h3 {
    font-size: $fz20;
    font-weight: $Bold;
    text-align: center;
    transition: transform 0.3s ease;
    transform-origin: center bottom;
    position: absolute;
    top: 110px;
    line-height: 1.5;
    padding: 0 6%;
  }

  .menu {
    list-style: none;
    padding-top: 100px;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease;
    text-align: center;

    li {
      padding: 2px 0;
    }

    .price {
      text-align: center;
      margin-top: 10px;
      font-weight: $Bold;
    }
  }

  .button {
    margin-bottom: 15px;
  }
}

// Mobile
@media screen and (max-width: 520px) {
  .card {
    &:hover {
      transform: translateY(0px);
    }
  }
}

//  Tablet SD
@media screen and (min-width: 521px) and (max-width: 768px) {
   .card {
    min-width: 200px;
       }
}
